/* Popular Recipes */
.wp-block-udemy-plus-popular-recipes {
    margin-bottom: 2rem;
  }
  .wp-block-udemy-plus-popular-recipes h6,
  .editor-styles-wrapper .wp-block-udemy-plus-popular-recipes h6 {
    margin-bottom: 1.25rem;
    margin-top: 0px;
    font-size: 1.25rem;
    line-height: 1.75rem;
    font-weight: 500;
  }
  .wp-block-udemy-plus-popular-recipes .single-post {
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
  }
  .wp-block-udemy-plus-popular-recipes .single-post-image {
    flex-shrink: 0;
  }
  .wp-block-udemy-plus-popular-recipes .single-post-image img {
    width: 4rem;
    border-radius: 0.5rem;
  }
  .wp-block-udemy-plus-popular-recipes .single-post-detail {
    margin-left: 1rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
  .wp-block-udemy-plus-popular-recipes .single-post-detail > a {
    display: block;
    font-weight: 500;
  }
  .wp-block-udemy-plus-popular-recipes .single-post-detail > span {
    --tw-text-opacity: 1;
    color: rgb(107 114 128 / var(--tw-text-opacity));
  }
  .container-flex {
    max-width: 800px;
    display: flex;
    justify-content: space-between;
  }
  .container-flex .item {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
  }
  .x-auto {
    margin: 0 auto;
  }
  .wr-select-form {
    display: flex;
    justify-content: space-between;
    max-width: 800px;
  }
  .wr-select-form select {
    display: block;
    width: 70%;
    height: 30px;
  }
  
  #wr-job-application {
    display: flex;
    height: 300px;
    flex-direction: column;
    justify-content: space-evenly;
    max-width: 800px;
    margin: 0 auto;
  }
  #wr-job-application input,
  #wr-job-application select,
  #wr-job-application label {
    display: block;
    width: 100%;
    max-width: 100%;
    height: 30px;
  }
  #wr-job-application button {
    margin-top: 20px;
  }